<template>
  <div>
    <h2>{{ this.plantInfo.crp_nm }} 기본 정보</h2>
    <div v-if="this.showPlantInfo == false">
      식물을 선택해보세요.
    </div>
    <div v-else>
      <!-- <li>사진 : {{ plantInfo.file_url }}</li> -->
      <div class="contents_div_2" style="padding:1rem;">
        <div v-if="this.plantImgUrl != ''">
          <img class="image_card" :src="this.plantImgUrl">
        </div>
        <div class="image_card" v-else>
          No Images.
        </div>
      </div>
      <div class="contents_div_2" style="padding:1rem;">
        <ul>
          <li>이름 : {{ plantInfo.crp_nm }}</li>
          <li>발아적절온도 : {{ plantInfo.grmntn_min_tmprtr_val }} ~ {{ plantInfo.grmntn_max_tmprtr_val }}</li>
          <li>생육적절온도 : {{ plantInfo.grwng_min_tmprtr_val }} ~ {{ plantInfo.grwng_min_tmprtr_val }}</li>
        </ul> 
      </div>
    </div>
  </div>
  
</template>

<script>
export default {
  props: {
    plantInfo: Object,
  },
  components: {
  },
  data() {
    return {
      plantImgUrl: '',
      showPlantInfo: false,
    };
  },
  created() {
    // console.log('created plantImgUrl ::: ', this.plantInfo); //xxxxxxxxxxxx
  },
  mounted() {
    // console.log('mounted plantImgUrl ::: ', this.plantInfo);//xxxxxxxxxxxx
  },
  updated() {
    if (typeof this.plantInfo.crp_nm === 'undefined') {
      this.showPlantInfo = false;
    } else {
      this.showPlantInfo = true;

      if (this.plantInfo.file_url != 'null') {
        // console.log('this.plantInfo.file_url is not null!!')
        try {
          this.plantImgUrl = require(`@/assets/images/${this.plantInfo.file_url}`);
          console.log('Get Image Url Success.')
        } catch(e) {
          // console.log(e.message);
          console.log("Image File ", this.plantInfo.file_url, 'is not exist.');
          this.plantImgUrl = '';
        }
        // console.log("this.plantImgUrl ::: ", this.plantImgUrl);
      } else {
        console.log('Props data plantInfo.file_url is null.')
        this.plantImgUrl = '';
      }
    }
  },
  methods: {
  },
};
</script>